<template>
  <div class="container" @mousemove="scrollToBottom" @keypress="scrollToBottom">
    <div class="header">
      <img src="../assets/icon.png" alt="icon" height="100" />
      <p>Chat App Anonim</p>
    </div>
    <div class="users">
     
      <div v-for="user in users" :key="user.index"><h4 :style="{'color':user.color}">{{user.username}}</h4></div>
     
    </div>
    <div id="message-area" class="message-area text-center">
      <div
        v-show="messages.length > 1"
        :style="{ 'background-color': message.user.color }"
        class="message"
        v-for="message in messages"
        :key="message.index"
      >
        <h4>{{ message.user.username }}</h4>
        <p>{{ message.message }}</p>
      </div>
    </div>
    <div :class="{ see: isActive }" class="type">{{ typing }}</div>
    <div class="message-input">
      <textarea
        type="text"
        v-model="message"
        @keypress.enter.prevent="sendMessage"
        @keypress="keyPressing"
      />
      <input type="submit" @click.prevent="sendMessage" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: "",
        messages: [],
        color: "",
      },
      users: [],
      message: "",
      messages: [],

      typing: "",
      isActive: false,
      colors: [
        "#e21400",
        "#91580f",
        "#f8a700",
        "#f78b00",
        "#58dc00",
        "#287b00",
        "#a8f07a",
        "#4ae8c4",
        "#3b88eb",
        "#3824aa",
        "#a700ff",
        "#d300e7",
      ],
    };
  },
  sockets: {
    //Sunucudan Gelen Emitler

    con(data) {
      console.log(data);
    },
    userLeft(data) {
      this.users.splice(this.users.indexOf(data), 1);
    },
    sendMessage(data) {
      this.user.messages.push(data.message);
      this.messages.push(data);
      this.scrollToBottom();
    },
    loggedIn(user) {
      this.users.push(user);
    },
    display(data) {
      if (data.typing) {
        this.typing = `${data.user.username} yazÄ±yor...`;
        this.isActive = true;
        setTimeout(() => {
          this.isActive = false;
        }, 3000);
      }
    },
  },

  methods: {
    getUserNameColor() {
      // Compute hash code
      var hash = 7;
      for (var i = 0; i < this.user.username.length; i++) {
        hash = this.user.username.charCodeAt(i) + (hash << 5) - hash;
      }
      // Calculate color
      var index = Math.abs(hash % this.colors.length);
      console.log(this.colors[index]);
      this.user.color = this.colors[index];
      return this.colors[index].toString();
    },
    keyPressing(e) {
      if (e.which != 13) {
        // Not EnterCode
        this.$socket.emit("typing", { user: this.user, typing: true });
      }
    },

    scrollToBottom() {
      var objDiv = document.getElementById("message-area");
      objDiv.scrollTop = objDiv.scrollHeight + objDiv.scrollWidth;
    },
    sendMessage() {
      console.log(this.users);
      if (this.message != "") {
        let data = {
          user: this.user,
          message: this.message,
          color: this.user.color,
        };
        console.log(data);
        this.$socket.emit("sendMessage", data);
        this.message = "";
      }
    },
    joinServer() {
      console.log(this.user);
      this.$socket.user = this.user;
      this.$socket.emit("loggedIn", this.user);
    },
  },
  mounted() {
    this.user.username = prompt("What is your Username", "Anonymous");
    if (!this.user.username) {
      this.user.username = "Anonymous";
    }
    this.joinServer();
    this.getUserNameColor();
  },
};
</script>

